import React, { Component } from 'react';
import {login} from "../../services/userWs"
import {Link} from "react-router-dom"

class AuthContainer extends Component {
    state={
        data:{}
    }

    handleChange = (event) =>{
        const {value, name} = event.target;
        let {data} = this.state;
        data[name] = value;
        this.setState({data});
    }

    onSubmit = (event) =>{
        event.preventDefault();
        console.log("Voy a enviar datos");
        login(this.state.data)
            .then((response) =>{
                console.log("Felicidades", response);
                this.setState({data:{}});
            })
            .catch(error=>{
                console.log("Hubo un error: ", error)
            });
        
    }

    render() {
        const {handleChange, onSubmit} = this;
        const {data} = this.state;
        return (
            <section className="uk-section">
                <div className="uk-container uk-flex uk-flex-center">
                    <div className="uk-width-1-4">
                        <h3>Bienveido </h3>
                        <form
                        onSubmit={onSubmit}
                         className="uk-width-1-1 uk-form-stacked uk-flex uk-flex-center uk-flex-column"
                        >
                            <div className="uk-margin">
                                <div className="uk-inline">
                                    <span className="uk-form-icon" uk-icon="icon: user"></span>
                                    <input className="uk-input" type="email" required name="email" onChange={handleChange}
                                        value={data["email"] ? data["email"] : ""}
                                    />
                                </div>
                            </div>

                            <div className="uk-margin">
                                <div className="uk-inline">
                                    <span className="uk-form-icon uk-form-icon-flip" uk-icon="icon: lock"></span>
                                    <input className="uk-input" type="text" required name="password" onChange={handleChange}
                                    value={data["password"] ? data["password"] : ""}
                                    />
                                </div>
                            </div>
                            <div className="uk-text-meta">
                                Aun no tienes cuenta? 
                                <Link className="uk-text-primary" to="/signup">
                                    Create una cuenta
                                </Link>
                            </div>
                            <button className="uk-button uk-button-primary">
                                Entrar
                            </button>
                        </form>
                    </div>
                </div>
            </section>
        );
    }
}

export default AuthContainer;